<script lang="coffeescript">

  import { onMount } from 'svelte'
  import Router from 'svelte-spa-router'

  import {  
    debug
    CssColors 
    CssFonts 
    CssResets 
    # CssResponsive
    CssTypography
    CssDarkMode
    Toast
    LayoutLeftSplit
    FavIconBadge 
  } from './Braintapper'
  
  import { 
    leftSplitContentElement
    SideNavigator
    CssGlobal
    PouchInit 
  } from "./Application"

  
  window.debug = true

  debug.on = window.debug




 

  
 


  # Routes

  import Radar from './Routes/Radar/Index.svelte'
  import Lists from './Routes/Lists/Index.svelte'
  import Favorites from './Routes/Favorites/Index.svelte'
  import Archived from './Routes/Archived/Index.svelte'
  import List from './Routes/Lists/Show.svelte'
  import Search from './Routes/Search/Index.svelte'
  import Tools from './Routes/Tools/Index.svelte'
  import Settings from './Routes/Settings/Index.svelte'
  import Sync from './Routes/Sync/Index.svelte'
  import Help from './Routes/Help/Index.svelte'
  import FourOFour from './Routes/FourOFour.svelte'


  showToast = false
  toastMessage = ""

  routes =
    '/': Radar
    '/lists': Lists
    '/favorites': Favorites
    '/archived': Archived
    '/lists/:id': List
    '/settings': Settings
    '/search': Search
    '/search/:terms': Search
    '/tools/': Tools
    '/sync': Sync
    '/help': Help
    '*': FourOFour


  appName = "Braintapper Radar Lists"



  contentEl  = undefined


  

  routeEvent = (e)-> #  router must be mounted or won't work
    console.error "routeEvent"
    console.debug e

  toaster = (e)->



  onMount ()->
    leftSplitContentElement.set contentEl
    window.mounted()

  `$: show = showToast`

</script>


<svelte:head>
  <title>{ appName }</title>
</svelte:head>

<PouchInit/>


<LayoutLeftSplit contentScrollable={true} on:toast={toaster}>
  <div slot="nav">
    <SideNavigator on:toast={toaster}/>
  </div>
  <div slot="default" style="padding: 48px 96px" bind:this={contentEl}>
    <Router {routes} on:routeEvent={routeEvent}/>
  </div>
</LayoutLeftSplit>
<Toast bind:show={showToast} message={toastMessage} delay={2}/>



